import React, { Component } from 'react';
import ReactPlayer from 'react-player';

export default class Landing extends Component {
  constructor(props) {
    super(props);
    this.state={};
  }

  render(){
    return(
      <div>
        <ReactPlayer url='../../../media/introVideo.mp4' playing='true' />
      </div>

    )
  }
}


